import React, { Component, Suspense } from 'react';
import { Accordion } from 'react-bootstrap';
import CodeMirror from "@uiw/react-codemirror";
import { tags as t } from '@lezer/highlight';
import { loadLanguage } from '@uiw/codemirror-extensions-langs';
import { createTheme } from '@uiw/codemirror-themes';
const myTheme = createTheme({
    theme: 'light',
    settings: {
        // foreground: '#75baff',
        caret: 'none',
        // selection: '#036dd626',
        // selectionMatch: '#036dd626',
        lineHighlight: '#8a91991a',
        // gutterBackground: '#f7f7f7',
        // gutterForeground: '#8a919966',
    },
    styles: [
        { tag: t.comment, color: '#b06213' },
        { tag: t.variableName, color: '#ff0000' },
        { tag: [t.string, t.special(t.brace)], color: '#b83b3b' },
        { tag: t.number, color: '#166948' },
        { tag: t.bool, color: '#92349f' },
        { tag: t.null, color: '#166948' },
        { tag: t.keyword, color: '#166948' },
        { tag: t.operator, color: '#166948' },
        { tag: t.className, color: '#ff0000' },
        { tag: t.definition(t.typeName), color: '#b83b3b' },
        { tag: t.typeName, color: '#b83b3b' },
        { tag: t.angleBracket, color: '#92349f' },
        { tag: t.tagName, color: '#4d9840' },
        { tag: t.attributeName, color: '#92349f' },
        { tag: t.color, color: '#3a2ba4' },
        { tag: t.color, color: '#3a2ba4' },
    ],
});
class CustomCss extends Component {
    render() {
        const { customCss, onClickEnableCustomCss, onChangeCss } = this.props
        return <Accordion.Item eventKey="7" className="pb-3">
            <Accordion.Header className="w-100">
                <span className="fs-9 fw-medium">Custom CSS</span>
            </Accordion.Header>
            <Accordion.Body className="border rounded-1 mt-1 p-2">
                <label className="form-check form-switch py-2 align-items-center justify-content-between mb-2 h-40px py-1">
                    <span className="form-check-label ps-0 fs-9 fw-medium text-gray-800">Enable Custom CSS</span>
                    <input className="form-check-input" type="checkbox" name="shareIcons" checked={customCss.cssStatus} onChange={onClickEnableCustomCss} />
                </label>
                <div className="w-100">
                    <CodeMirror
                        value={customCss.css}
                        height="350px"
                        theme={myTheme}
                        //   theme={''}
                        basicSetup={{
                            foldGutter: false,
                            dropCursor: false,
                            allowMultipleSelections: false,
                            indentOnInput: false,
                            autocompletion: false
                        }}

                        extensions={[loadLanguage('css')]}
                        onChange={onChangeCss}


                    />
                </div>
            </Accordion.Body>
        </Accordion.Item>
    }
}
export default CustomCss